@import 'assets/scss/utils/mixins';

.container {
	max-width: 1000px;
}

.section-spacing {
	margin-bottom: 16px;
}

findqo-other-ad-list,
findqo-other-ad-list-skeleton-loader {
	display: block;
	margin: 16px 0;
}

.horizontal-line {
	width: 100px;
	height: 5px;
	opacity: 15%;
	background-color: var(--prim-2);
	border-radius: 100px;
}

.my-32px {
	margin-top: 32px !important;
	margin-bottom: 32px !important;
}

@include media-breakpoint-up(md) {
	.section-spacing {
		margin-bottom: 32px;
	}

	findqo-other-ad-list,
	findqo-other-ad-list-skeleton-loader {
		margin: 32px 0;
	}
}

.download-app {
	margin-bottom: 16px;

	@include media-breakpoint-up(sm) {
		margin-bottom: 32px;
		margin-top: 32px;
	}
}

.gradient-banner {
	width: 100%;
	height: 200px;
	border-radius: 8px;
	background: linear-gradient(135deg,
		var(--prim-2) 0%,
		var(--prim-1) 20%,
		var(--prim-3) 800%
	);
	transition: transform 0.3s ease;

	&:hover {
		transform: scale(1.02);
	}

	@include media-breakpoint-up(md) {
		height: 300px;
	}
}
